import React, { useState, useEffect } from 'react';
import ScanBarcodeButton from './components/ScanBarcodeButton';
import AddProductForm from './components/AddProductForm';
import AddProductInventoryForm from './components/AddProductInventoryForm';
import ProductItem from './components/ProductItem';
import ShoppingListItem from './components/ShoppingListItem';
import ExpiredProductItem from './components/ExpiredProductItem';
import { SettingsModal } from './components/SettingsModal';
import { supabase } from './utils/supabaseClient';
import ProductSearchBar from './components/ProductSearchBar';




const App = () => {
  const [products, setProducts] = useState([]);
  const [shoppingList, setShoppingList] = useState([]);
  const [expiredProducts, setExpiredProducts] = useState([]);
  const [activeTab, setActiveTab] = useState('inventory');
  const [showAddForm, setShowAddForm] = useState(false);
  const [editingProduct, setEditingProduct] = useState(null);
  const [showAddInventoryForm, setShowAddInventoryForm] = useState(false);
  const [editingProductShopping, setEditingProductShopping] = useState(null);
  const [codigoEscaneado, setCodigoEscaneado] = useState('');


  
  const fetchProducts = async () => {
    const { data, error } = await supabase.from('productos').select("id, nombre, codigo_barras, cantidad, fecha_caducidad, categorias (nombre), ubicaciones (nombre)");
    if (!error) {
      setProducts(data);
    }
  };

  const fetchExpiredProducts = async () => {
    const { data, error } = await supabase
      .from('caducados')
      .select("id, nombre, cantidad, fecha_caducidad, categorias (nombre), ubicaciones (nombre)");

    if (!error) {
      setExpiredProducts(data);
    }
  };

  const fetchShoppingList = async () => {
    const { data, error } = await supabase
      .from('lista_compra')
      .select("*");

    if (!error) {
      setShoppingList(data);
    }
  };

  if (activeTab === 'shopping'){
    fetchShoppingList();
  }
  if (activeTab === 'inventory'){
    fetchProducts();
  }
  if (activeTab === 'expired'){
    fetchExpiredProducts();
  }

  useEffect(() => {
    fetchProducts();
    fetchExpiredProducts();
    fetchShoppingList();
    revisarProductosCaducados();
  }, []);

  // Efecto para manejar productos caducados
  
  const revisarProductosCaducados = async () => {
    const now = new Date();

    const { data: productos, error } = await supabase
      .from('productos')
      .select('*');

    if (error) {
      console.error('Error al obtener productos:', error.message);
      return;
    }

    const caducados = productos.filter(p => new Date(p.fecha_caducidad) < now);
    //const vigentes = productos.filter(p => new Date(p.fecha_caducidad) >= now);

    // Eliminar los caducados del inventario
    for (const producto of caducados) {
      await supabase.from('productos').delete().eq('id', producto.id);
    }

    // Agregarlos a 'lista_compra' y 'caducados'
    const nuevoId = Date.now().toString();
    

    for (const producto of caducados) {
      // Obtener nombre de categor√≠a
      const { data: categoriaData } = await supabase
        .from('categorias')
        .select('nombre')
        .eq('id', producto.categoria_id)
        .single();

      //const categoriaNombre = categoriaData?.nombre || '';

      // Obtener nombre de ubicaci√≥n
      const { data: ubicacionData } = await supabase
        .from('ubicaciones')
        .select('nombre')
        .eq('id', producto.ubicacion_id)
        .single();

      //const ubicacionNombre = ubicacionData?.nombre || '';

      const { data: existe, error: errorExiste } = await supabase
        .from('lista_compra')
        .select('id')
        .eq('codigo_barras', producto.codigo_barras)
        .eq('es_caducado', true)
        .maybeSingle();

      if (!existe) {
        
        await supabase.from('lista_compra').insert([{
          id: nuevoId,
          ...producto,
          es_caducado: true,
        }]);
      }


      // Insertar en caducados
      const yaExiste = await supabase
        .from('caducados')
        .select('id')
        .eq('nombre', producto.nombre)
        .eq('codigo_barras', producto.codigo_barras)
        .eq('fecha_caducidad', producto.fecha_caducidad);

      if (yaExiste.data.length === 0) {
        await supabase.from('caducados').insert({
          nombre: producto.nombre,
          cantidad: producto.cantidad,
          codigo_barras: producto.codigo_barras,
          fecha_caducidad: producto.fecha_caducidad,
          categoria_id: producto.categoria_id,
          ubicacion_id: producto.ubicacion_id
      });
      }
    }

    // Recarga de estado tras limpieza
    fetchProducts();
    fetchExpiredProducts();
    fetchShoppingList();
  };


  const handleScan = (barcode) => {
    setShowAddForm(true);
    setEditingProduct({
      name: '',
      barcode,
      quantity: 1,
      category: 'otros',
      location: 'despensa-estante1',
      expiryDate: ''
    });
  };

  const handleAddProduct = (newProduct) => {
    if (editingProduct && editingProduct.id) {
      // Actualizar producto existente
      setProducts(products.map(p => 
        p.id === editingProduct.id ? { ...newProduct, id: editingProduct.id } : p
      ));
    } else {
      // A√±adir nuevo producto
      setProducts([...products, { ...newProduct, id: Date.now().toString() }]);
    }
    fetchProducts();
    setShowAddForm(false);
    setEditingProduct(null);
    revisarProductosCaducados();
  };

  const handleScanShopping = (codigo) => {
    setCodigoEscaneado(codigo);
    setShowAddInventoryForm(true);
  };
  
  const handleAddInventoryProduct = (newProduct) => {
    if (editingProductShopping && editingProductShopping.id) {
      // Actualizar producto existente
      setProducts(products.map(p => 
        p.id === editingProductShopping.id ? { ...newProduct, id: editingProductShopping.id } : p
      ));
    } else {
    // A√±adir nuevo producto a la lista de la compra.
    setProducts([...products, { ...newProduct, id: Date.now().toString() }]);
    }
    fetchShoppingList();
    setEditingProductShopping(null);
    setShowAddInventoryForm(false);
  };

  const handleEditProduct = (producto) => {
    console.log('üìù Editando producto:', producto);
    setEditingProduct({
      id: producto.id,               // üëà
      name: producto.nombre,
      barcode: producto.codigo_barras,
      quantity: producto.cantidad,
      expiryDate: producto.fecha_caducidad,
      category: producto.categoria_id,
      location: producto.ubicacion_id
    });
    setShowAddForm(true);
    // üëá Scroll al principio de la p√°gina
    setTimeout(() => {
      window.scrollTo({ top: 0, behavior: 'smooth' });
    }, 100); // esperar a que se muestre el formulario
  };


  const handleDeleteProduct = async (productId) => {
    const { error } = await supabase
    .from('productos')
    .delete()
    .eq('id', productId);

    if (error) {
      console.error('‚ùå Error al eliminar producto:', error.message);
      alert('‚ùå No se pudo eliminar el producto');
    } else {
      // Eliminarlo del estado para actualizar la vista
      setProducts((prevProducts) => prevProducts.filter(p => p.id !== productId));
    }
    fetchProducts();
  };
  

  const handleDeleteExpiredProduct = async (product) => {
    const { error } = await supabase
      .from('caducados')
      .delete()
      .eq('id', product.id);

    if (!error) {
      setExpiredProducts(prev => prev.filter(p => p.id !== product.id));
    }
    fetchExpiredProducts();
  };

  const handleAddToShoppingList = async (product) => {
    const yaExiste = await supabase
      .from('caducados')
      .select('id')
      .eq('nombre', product.nombre)
      .eq('codigo_barras', product.codigo_barras)
      .eq('fecha_caducidad', product.fecha_caducidad);

    if (yaExiste.data.length === 0) {
      await supabase.from('lista_compra').insert({
        nombre: product.nombre,
        cantidad: product.cantidad,
        codigo_barras: product.codigo_barras,
        fecha_caducidad: product.fecha_caducidad,
        categoria_id: product.categoria_id,
        ubicacion_id: product.ubicacion_id,
        creado: product.created_at,
        //es_caducado: true
      });
    }
  };

  const handleAddManualItem = () => {
    setShowAddInventoryForm(true);
  };


  const handleTogglePurchased = async (product) => {
    const { error } = await supabase
      .from('lista_compra')
      .delete()
      .eq('id', product.id);
    if (!error) {
    setShoppingList(shoppingList.filter(i => i.id !== product.id));
    }
    fetchShoppingList();
  };

  //A√±adir y Eliminar cantidades para la lista de la compra.
  const handleAddQuantity = async (product) => {
    const { error } = await supabase
      .from('lista_compra')
      .update({ cantidad: product.cantidad + 1})
      .eq('id', product.id);
    if (!error) {
    setShoppingList(shoppingList.filter(i => i.id !== product.id));
    }
    fetchShoppingList();
  };

  const handleSubstractQuantity = async (product) => {
    const { error } = await supabase
      .from('lista_compra')
      .update({ cantidad: product.cantidad - 1})
      .eq('id', product.id);
    if (!error) {
    setShoppingList(shoppingList.filter(i => i.id !== product.id));
    }
    if (product.cantidad === 1){
      const { error } = await supabase
      .from('lista_compra')
      .delete()
      .eq('id', product.id);
    if (!error) {
    setShoppingList(shoppingList.filter(i => i.id !== product.id));
    }
    }
    fetchShoppingList();
  };

  //A√±adir y eliminar cantidades para el inventario.
  const handleAddQuantityInventario = async (product) => {
    const nuevaCantidad = product.cantidad + 1;
    const { error } = await supabase
      .from('productos')
      .update({ cantidad: nuevaCantidad})
      .eq('id', product.id);
    if (!error) {
      //alert('‚ùå No se pudo a√±adir la cantidad');
      setProducts(products.map(p =>
        p.id === product.id ? { ...p, cantidad: nuevaCantidad } : p
      ));

      if (searchResults.length > 0) {
        const nuevosResultados = searchResults.map(p =>
          p.id === product.id ? { ...p, cantidad: nuevaCantidad } : p
        );
        setSearchResults(nuevosResultados);
      }
    }
    fetchProducts();
  };

  const handleSubstractQuantityInventario = async (product) => {
    const nuevaCantidad = product.cantidad - 1;
    const { error } = await supabase
      .from('productos')
      .update({ cantidad: nuevaCantidad})
      .eq('id', product.id);
      if (!error) {
        
        //alert('‚ùå No se pudo restar la cantidad');
        setProducts(products.map(p =>
          p.id === product.id ? { ...p, cantidad: nuevaCantidad } : p
        ));

        if (searchResults.length > 0) {
          const nuevosResultados = searchResults.map(p =>
            p.id === product.id ? { ...p, cantidad: nuevaCantidad } : p
          );
          setSearchResults(nuevosResultados);
        }
      }
      if (product.cantidad === 1){
        const { error } = await supabase
        .from('productos')
        .delete()
        .eq('id', product.id);
      if (!error) {
        //alert('‚ùå No se pudo eliminar la cantidad');
        setProducts(products.filter(i => i.id !== product.id));
      }
    }
    fetchProducts();
  };

  const [mostrarAjustes, setMostrarAjustes] = useState(false);
  
  //Barra de b√∫squeda
  const [searchResults, setSearchResults] = useState([]);
  const [catalogMatch, setCatalogMatch] = useState(null);
  const [searchTerm, setSearchTerm] = useState('');

  const handleAddCatalogProductToShoppingList = async () => {
    if (!catalogMatch) return;

    const { error } = await supabase.from('lista_compra').insert({
      nombre: catalogMatch.nombre,
      cantidad: 1,
      codigo_barras: catalogMatch.codigo_barras,
      categoria_id: catalogMatch.categoria_id,
      ubicacion_id: catalogMatch.ubicacion_id,
      es_caducado: false
    });

    if (error) {
      console.error('‚ùå Error al a√±adir desde cat√°logo:', error.message);
      alert('‚ùå No se pudo a√±adir el producto desde el cat√°logo.');
    } else {
      alert(`‚úÖ A√±adido "${catalogMatch.nombre}" a la lista de la compra`);
      fetchShoppingList(); // refrescar la lista
      setCatalogMatch(null); // limpia la sugerencia
      setSearchResults([]);  // limpia los resultados
    }
  };






  return (
    <div className="min-h-screen bg-gray-800 p-4">
      <div className="max-w-6xl mx-auto bg-white rounded-lg shadow overflow-hidden">
        <div className="p-4 bg-blue-600 text-white">
          <div className="flex items-center justify-between">
            <h1 className="text-2xl font-bold">SmartFridge</h1>
            <button
              onClick={() => setMostrarAjustes(true)}
              className="bg-gray-700 text-white px-3 py-2 rounded hover:bg-gray-700"
            >
              ‚öôÔ∏è Ajustes
            </button>
          </div>
          
          <p className="text-blue-100">Control total de tu cocina</p>
          
          

          {mostrarAjustes && <SettingsModal onClose={() => setMostrarAjustes(false)} />}
          
        </div>
        
        <div className="flex border-b">
          <button
            className={`flex-1 py-2 px-4 font-medium ${activeTab === 'inventory' ? 'text-blue-600 border-b-2 border-blue-600' : 'text-gray-600'}`}
            onClick={() => setActiveTab('inventory')}
          >
            Inventario üìã‚Äã
          </button>
          <button
            className={`flex-1 py-2 px-4 font-medium ${activeTab === 'shopping' ? 'text-blue-600 border-b-2 border-blue-600' : 'text-gray-600'}`}
            onClick={() => setActiveTab('shopping')}
          >
            Lista de la compra üõí
          </button>
          <button
            className={`flex-1 py-2 px-4 font-medium ${activeTab === 'expired' ? 'text-blue-600 border-b-2 border-blue-600' : 'text-gray-600'}`}
            onClick={() => setActiveTab('expired')}
          >
            Caducados ({expiredProducts.length}) ‚ò£Ô∏è‚Äã
          </button>
        </div>
        
        <div className="p-4">
          {activeTab === 'inventory' ? (
            <>
              <div>
                <ProductSearchBar onResults={(resultados, catalogoArray, termino) => {
                  setSearchResults(resultados);
                  setCatalogMatch(Array.isArray(catalogoArray) ? catalogoArray : []);
                  setSearchTerm(termino); //guarda el t√©rmino para futuras refiltraciones
                }} />
              </div>
              <div className="flex space-x-2 mb-4">
                <ScanBarcodeButton onScan={handleScan} />
                <button
                  onClick={() => {
                    setShowAddForm(true);
                    setEditingProduct(null);
                    fetchProducts();
                  }}
                  className="bg-green-500 text-white py-2 px-4 rounded-lg hover:bg-green-600 transition-colors"
                >
                  A√±adir Manualmente
                </button>
              </div>
              
              {showAddForm && (
                <div className="mb-6 p-4 border rounded-lg bg-gray-50">
                  <AddProductForm onProductAdded={handleAddProduct} 
                    Inventario={editingProduct} 
                  />

                  <button
                    onClick={() => {
                      setShowAddForm(false);
                      setEditingProduct(null);
                      fetchProducts();
                    }}
                    className="mt-2 text-sm text-gray-600 hover:text-gray-800"
                  >
                    Cancelar
                  </button>
                </div>
              )}
              
              <div className="space-y-4">
                {searchResults.length === 0 && Array.isArray(catalogMatch) && catalogMatch.length > 0 && (
                  <div>
                    <p className="text-yellow-600 font-semibold mt-2">
                      Los siguientes productos est√°n agotados:
                    </p>
                    {/*<div className="mt-4 flex space-x-2">
                      <p className="text-yellow-600 font-semibold mt-2">
                        ¬øA√±adir "{catalogMatch.nombre}" a la lista de la compra?</p>
                      
                      <button
                        onClick = {handleAddCatalogProductToShoppingList} className="text-sm bg-blue-400 text-white py-1 px-3 rounded hover:bg-blue-600"
                      >
                      üõí
                      </button>
                    </div>*/}
                    <ul className="mt-2 space-y-2">
                      {catalogMatch.map((prod) => (
                        <li key={prod.id} className="flex items-center justify-between bg-yellow-100 p-2 rounded">
                          <span>{prod.nombre}</span>
                          <button
                            onClick={async () => {
                              const { error } = await supabase.from('lista_compra').insert({
                                nombre: prod.nombre,
                                cantidad: 1,
                                codigo_barras: prod.codigo_barras,
                                categoria_id: prod.categoria_id,
                                ubicacion_id: prod.ubicacion_id,
                                es_caducado: false
                              });

                              if (!error) {
                                alert(`‚úÖ A√±adido "${prod.nombre}" a la lista de la compra`);
                                fetchShoppingList();
                              } else {
                                alert('‚ùå Error al a√±adir a la lista de la compra');
                              }
                            }}
                            className="text-sm bg-blue-500 text-white py-1 px-3 rounded hover:bg-blue-600"
                          >
                            A√±adir üõí
                          </button>
                        </li>
                      ))}
                    </ul>
                  </div>
                )}
                {(searchResults.length > 0 ? searchResults : products).length === 0 ? (
                  <p className="text-gray-500">No hay productos en tu inventario. ¬°A√±ade algunos!</p>
                ) : (
                  (searchResults.length > 0 ? searchResults : products).map(product => (
                    <ProductItem
                      key={product.id}
                      product={product}
                      onEdit={handleEditProduct}
                      onDelete={handleDeleteProduct}
                      onAddToShoppingList={handleAddToShoppingList}
                      onTogglePlus={handleAddQuantityInventario}
                      onToggleMinus={handleSubstractQuantityInventario}
                    />
                  ))
                )}

                
              </div>
            </>
          ) : activeTab === 'shopping' ? (
            <>
              <div className="flex space-x-2 mb-4">
                <ScanBarcodeButton onScan={(code) => {
                  setCodigoEscaneado(code);
                  setShowAddInventoryForm(true);
                }} />
                <button
                  onClick={() => {
                    setShowAddInventoryForm(true);
                    setCodigoEscaneado('');
                    setEditingProductShopping(null);
                    fetchShoppingList();
                  }}
                  className="bg-green-500 text-white py-2 px-4 rounded-lg hover:bg-green-600 transition-colors"
                >
                  A√±adir Producto
                </button>
              </div>
              {showAddInventoryForm && (
                <div className="mb-6 p-4 border rounded-lg bg-gray-50">
                  <AddProductInventoryForm
                    codigoInicial={codigoEscaneado}
                    onProductAdded={() => {
                      setShowAddInventoryForm(false);
                      setCodigoEscaneado('');
                      fetchShoppingList(); // si quieres refrescar despu√©s de a√±adir
                    }}
                  />
                  <button
                    onClick={() => {
                      setShowAddInventoryForm(false);
                      fetchShoppingList();
                    }}
                    className="mt-2 text-sm text-gray-600 hover:text-gray-800"
                  >
                    Cancelar
                  </button>
                </div>
              )}
              <div className="space-y-4">
                
                {shoppingList.length === 0 ? (
                  <p className="text-gray-500">Tu lista de la compra est√° vac√≠a. ¬°A√±ade algunos productos!</p>
                ) : (
                  shoppingList.map(product => (
                    <ShoppingListItem
                      key={product.id}
                      product={product}
                      onTogglePurchased={handleTogglePurchased}
                      onTogglePlus={handleAddQuantity}
                      onToggleMinus={handleSubstractQuantity}
                    />
                  ))
                )}
              </div>
            </>
          ) : (
            <>
              <h2 className="text-xl font-semibold mb-4">Productos Caducados</h2>
              
              <div className="space-y-4">
                {expiredProducts.length === 0 ? (
                  <p className="text-gray-500">¬°Genial! No tienes productos caducados.</p>
                ) : (
                  
                  expiredProducts.map(product => (
                    <ExpiredProductItem
                      key={product.id}
                      product={product}
                      onDelete={handleDeleteExpiredProduct}
                      onAddToShoppingList={handleAddToShoppingList}
                    />
                  ))
                )}
              </div>
            </>
          )}
        </div>
        {/* Pie opcional */}
        <div className="p-2 border-t text-center text-sm text-gray-400">
          ¬© SmartFridge
        </div>
      </div>
    </div>
  );
};

export default App;