import React, { useState, useEffect } from 'react';
import { supabase } from '../utils/supabaseClient';

const ProductSearchBar = ({ onResults }) => {
  const [query, setQuery] = useState('');
  const [categoriaId, setCategoriaId] = useState('');
  const [ubicacionId, setUbicacionId] = useState('');
  const [categorias, setCategorias] = useState([]);
  const [ubicaciones, setUbicaciones] = useState([]);

  useEffect(() => {
    const fetchFilters = async () => {
      const { data: cat } = await supabase.from('categorias').select('*');
      const { data: ubi } = await supabase.from('ubicaciones').select('*');
      setCategorias(cat);
      setUbicaciones(ubi);
    };
    fetchFilters();
  }, []);

  const handleSearch = async () => {
    let queryBase = supabase
      .from('productos')
      .select('*, categorias(nombre), ubicaciones(nombre)')
      .or(`nombre.ilike.%${query}%,codigo_barras.ilike.%${query}%`);

    if (categoriaId) queryBase = queryBase.eq('categoria_id', categoriaId);
    if (ubicacionId) queryBase = queryBase.eq('ubicacion_id', ubicacionId);

    const { data: productos, error } = await queryBase;

    if (error) return alert('‚ùå Error buscando productos');

    if (productos.length > 0) {
      onResults(productos, null); // ‚úÖ A√±adimos el t√©rmino de b√∫squeda
    } else {
        const { data: enCatalogo, error: errorCatalogo } = await supabase
            .from('catalogo_productos')
            .select('*')
            .or(`nombre.ilike.%${query}%,codigo_barras.ilike.%${query}%`);
        
        if (errorCatalogo) {
            alert('‚ùå Error al buscar en el cat√°logo');
            return;
        }
        
        onResults([], enCatalogo); // ‚Üê ‚úÖ Pasar todos
        
    }
  };

  return (
    <div className="flex flex-col md:flex-row gap-2 mb-4">
      <input
        type="text"
        placeholder="Buscar por nombre o c√≥digo de barras"
        value={query}
        onChange={(e) => setQuery(e.target.value)}
        className="border px-3 py-2 rounded w-full"
      />
      <select
        value={categoriaId}
        onChange={(e) => setCategoriaId(e.target.value)}
        className="border px-3 py-2 rounded"
      >
        <option value="">Todas las categor√≠as</option>
        {categorias.map(c => <option key={c.id} value={c.id}>{c.nombre}</option>)}
      </select>
      <select
        value={ubicacionId}
        onChange={(e) => setUbicacionId(e.target.value)}
        className="border px-3 py-2 rounded"
      >
        <option value="">Todas las ubicaciones</option>
        {ubicaciones.map(u => <option key={u.id} value={u.id}>{u.nombre}</option>)}
      </select>
      <button onClick={handleSearch} className="text-sm bg-blue-500 text-white py-2 px-3 rounded hover:bg-blue-600">
        Buscar <span role="img">üîç</span> 
      </button>
    </div>
  );
};

export default ProductSearchBar;
